body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

/*---*/

body {
  font-family: 'Gilroy-regular', sans-serif;
  color: #333333;
}

.App {
  position: relative;
  overflow: hidden;
}

a,
a:visited {
  color: #333333;
}

@font-face {
  font-family: "Gilroy-Regular";
  src: url("./fonts/Gilroy-Regular.ttf");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Gilroy-Medium";
  src: url("./fonts/Gilroy-Medium.ttf");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Gilroy-Semibold";
  src: url("./fonts/Gilroy-Semibold.ttf");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "NotoSerif-Bold";
  src: url("./fonts/NotoSerif-Bold.ttf");
  font-style: normal;
  font-weight: normal;
}

.wrapper {
  max-width: 1130px;
  padding: 0 20px;
  margin: 0 auto;
}

h2 {
  font-family: 'NotoSerif-Bold';
  font-size: 30px;
  line-height: 41px;
  text-align: center;
}

h3 {
  font-family: 'Gilroy-Regular';
  font-weight: 400;
  font-size: 21px;
  line-height: 140%;
  text-align: center;
}

@media (max-width: 290px) {
  h2 {
    font-size: 25px;
  }

  h3 {
    font-size: 18px;
  }
}